body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.page-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.item-box {
  width: 100%;
  height: 75px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-weight: bold;
  font-size: 2rem;
  color: #fff;
  background: #f66a;
}

label {
  font-size: 1.6rem;
}

input[type='number'] {
  height: 40px;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 1em;
  border-radius: 6px;
}

.slideable-container {
  border: 5px solid #0000;
  margin-bottom: 0.5em;
}
